@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  font-size: 20px;
  font-weight: 600;

  --bg-color: #383838;
  --bg-blocks: #383838;
  --bg-day: #373636;
  --bg-night: #373636;
  --bg-search: #444;
  --bg-even: rgb(64 64 64);
  --placeholder-col: rgb(255 255 255 / 0.6);
  --mode-color: #ffffff;

  background: var(--bg-color);
}

body.light {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #292929;
  font-size: 20px;
  font-weight: 600;

  --bg-color: linear-gradient(135deg, #d1c5c5 0%, #466173 100%);
  --bg-blocks: #D9D9D9;
  --bg-day: linear-gradient(145deg, #e48724 0%, #f6fad9 100%);
  --bg-night: linear-gradient(155deg, #443d64 0%, #6582c6 100%);
  --bg-search: #D9D9D9;
  --bg-even: #D9D9D9;
  --placeholder-col: rgb(41 41 41 / 0.6);
  --mode-color: #000000;


  background: var(--bg-color);
}






.background-day {
  background: var(--bg-day);
}

.background-night {
  background: var(--bg-night);
}


